html, body, #root, .container {
  height: 100%;
  font-family: 'Roboto', sans-serif;
}
.App{
  height: 100%;
}
.align-center{
  text-align: center;
  padding: 3%;
}
.form{
  display: -webkit-flexbox;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.form-wrap{
  width: 40%;
}
.search-container {
  text-align: right;
  margin: 22px 100px;
  position: relative;
}
.search-icon{
  position: absolute;
  right: 1.5%;
  top: 20%;
}
.spinner{
  position: absolute;
  top: 35%;
  right: 50%;
  color: #3838d6;
}
.search-input {
  width: 400px;
}
.alert-wrap{
  position: absolute;
  width: 100%;
  z-index: 1;
}
.redirect{
  color: #5656ff;
  padding-top: 10px;
  cursor: pointer;
}
.header{
  text-align: center;
  padding-bottom: 15px;
  font-weight: 400;
}